  display: block;
  height: 100%;
  width: 100%;
  line-height: 40px;
  text-align: center;
  font-size: 14px;
}
.footer__menu .footer-menu__category--submenu .footer-submenu :nth-child(2), .footer__menu .footer-menu__category--submenu .footer-submenu :nth-child(3) {
  border-top: none;
}

.footer-menu__category a:hover {
  color: #055A81;
}

.footer-menu__category--submenu:hover .footer-submenu {
  opacity: 1;
}

